body {
   min-width: 320px;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
   @include f('os400', 16, 24);
   color: $black;
   &.is-locked {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      height: 0;
      overflow: hidden; } }
.out {
   overflow: hidden; }

.container {
   max-width: 1200px;
   margin: 0 auto;
   padding: 0 15px; }

.row {
   display: flex;
   flex-wrap: wrap;
   margin: 0 -15px;
   @include xs {
      flex-direction: column; } }

.col {
   flex: 0 1 25%;
   padding: 0 15px;
   .is-ie & {
      max-width: 292px; }
   @include md {
      padding: 0 10px;
      flex: 0 1 33.33333%; }
   @include sm {
      flex: 1 1 auto; }
   &:nth-child(1) ~ & {
      @include sm {
         padding: 30px 15px 0; } }
   &:nth-child(4) ~ & {
      padding: 30px 15px 0; }
   &:nth-child(3) ~ & {
      @include md {
         padding: 20px 10px 0; } } }

.section {
   padding: 65px 0 80px;
   @include sm {
      padding: 50px 0 60px; } }

.out {
   overflow: hidden; }

.title {
   @include f('os800', 35, 46);
   text-transform: uppercase;
   text-align: center;
   @include sm {
      font-size: 26px;
      line-height: 34px; }
   span {
      color: #0057ae; } }
.title-md {
   @extend .title;
   font-size: 21px;
   @include sm {
      font-size: 18px;
      line-height: 24px; } }

.btn {
   display: inline-block;
   outline: 0;
   text-align: center;
   @include f('os600', null, 20);
   background: $color-1;
   border-radius: 21px;
   letter-spacing: 0.03em;
   @include trs;
   &_md {
      padding: 11px 36px;
      font-size: 15px; }
   &_lg {
      min-width: 370px;
      padding: 16px 30px;
      font-size: 18px;
      border-radius: 27px;
      @include xs {
         min-width: 250px;
         padding: 15px;
         font-size: 16px; } }
   &_blue {
      color: $white;
      background: $color-1;
      &:hover {
         background: darken($color-1, 8); } }
   &_black {
      color: $black;
      border-color: $black;
      &:hover {} } }

.section-lines {
   position: relative;
   border-top: 1px solid rgba($color-1, .3);
   &::before, &::after {
      position: absolute;
      left: 0;
      width: 100%;
      content: ''; }
   &::before {
      top: 14px;
      border-top: 2px solid rgba($color-1, .3); }
   &::after {
      top: 30px;
      border-top: 3px solid rgba($color-1, .3); } }

.text-label {
   position: absolute;
   left: -4px;
   min-width: 373px;
   padding: 8px 15px 8px 35px;
   font-size: 14px;
   @include f('os800');
   text-transform: uppercase;
   box-shadow: 0 0 7px 1px rgba(0, 0, 0, 0.45);
   border-radius: 0 0 0 5px;
   letter-spacing: 0.03em;
   &::before {
      position: absolute;
      top: -4px;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
      border-radius: 4px;
      background-color: #d6d6d6;
      content: ''; }
   &_white {
      color: #000;
      background-color: #fff; }
   &_blue {
      color: #fff;
      background-color: #0057ae; } }
