@import "../helpers/all";

.header {
   $parent: &;
   position: relative;
   z-index: 100;
   @include sm {
      height: 68px; }
   &__line {
      padding: 10px 0 15px;
      text-align: center;
      background-color: #333333;
      letter-spacing: 0.38px;
      font-size: 15px;
      color: $white;
      @include md {
         padding: 8px 0;
         font-size: 12px; }
      @include sm {
         display: none; } }
   &__info {
      padding: 16px 0 18px;
      @include sm {
         padding: 13px 0; }
      &-container {
         display: flex;
         justify-content: space-between;
         align-items: center; } }
   &__logo {
      flex: 0 0 198px; }

   &__call-btn {
      min-width: 223px;
      padding: 11px 20px;
      @include xs {
         display: none; }
      &_mob {
         display: none;
         margin-top: 30px;
         @include xs {
            display: block;
            max-width: 250px;
            margin: 30px auto 0; } } }
   &__nav {
      padding: 17px 0 20px;
      border-top: 1px solid $color-5;
      @include sm {
         display: flex;
         flex-direction: column;
         justify-content: center;
         position: absolute;
         top: 68px;
         left: 0;
         width: 100%;
         height: 100vh;
         z-index: 10;
         overflow: auto;
         opacity: 0;
         border: 0;
         padding: 0 0 68px;
         visibility: hidden;
         transform: translate3d(0, 100px, 0);
         transition: transform .4s ease, opacity .4s ease, visibility 0s .4s, -webkit-transform .4s ease;
         text-align: center;
         background: #fff;
         box-shadow: inset 0 0 10px rgba(#000, .5); }
      &.is-active {
         opacity: 1;
         visibility: visible;
         transform: translate3d(0, 0, 0);
         transition: transform .4s ease, opacity .4s ease, visibility 0s 0s, -webkit-transform .4s ease; }
      &-container {
         @include sm {
            width: 100%; } }

      &-list {
         display: flex;
         justify-content: space-between;
         @include sm {
            flex-direction: column;
            justify-content: center;
            align-items: center;
            overflow: auto; }
         li {
            &:not(:last-child) {
               @include sm {
                  margin-bottom: 20px; } }
            a {
               @include f('os700', 14, 14);
               text-transform: uppercase;
               color: $black;
               @include trs;
               &:hover {
                  color: $color-1; }
               @include sm {
                  font-size: 16px; } } } } }
   &__nav-btn {
      $parent: &;
      display: none;
      position: relative;
      top: -8px;
      border: 0;
      outline: 0;
      padding: 0;
      margin-left: 30px;
      z-index: 11;
      @include sm {
         display: block; }
      @include xs {
         top: -5px; }

      &-burger {
         margin: auto;
         width: 40px;
         height: 40px;
         cursor: pointer; }
      &-lines {
         width: 40px;
         height: 3px;
         &:after, &:before {
            width: 40px;
            height: 3px; }
         position: relative;
         transform: translateY(25px);
         background: $color-1;
         transition: all .3s;
         #{$parent}.is-active & {
            background: rgba(255, 255, 255, 0); }
         &:before {
            content: "";
            position: absolute;
            left: 0;
            bottom: 10px;
            background: $color-1;
            transition: bottom 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1); }
         &:after {
            content: "";
            position: absolute;
            left: 0;
            top: 10px;
            background: $color-1;
            transition: top 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1); }
         #{$parent}.is-active & {
            &:after {
               top: 0;
               transform: rotate(45deg);
               transition: top 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1); }
            &:before {
               bottom: 0;
               transform: rotate(-45deg);
               transition: bottom 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1); } } } } }
