//@import helpers/all

.footer {
   &__container {}
   &__info {
      padding: 40px 0;
      background-color: #333333;
      color: $white;

      &-row {
         margin: 0;
         justify-content: space-between;
         @include sm {
            margin: 0 -10px; } }
      &-col {
         flex: 0 1 300px;
         @include sm {
            flex: 0 1 33.333333%;
            padding: 0 10px; }
         &:not(:last-child) {
            @include xs {
               margin-bottom: 30px; } }
         &:nth-child(1) {
            flex: 0 1 270px;
            @include sm {
               flex: 0 1 33.333333%; } }
         &:nth-child(2) {
            margin-left: 30px;
            @include sm {
               margin-left: 0; } } }

      &-title {
         margin-bottom: 15px;
         padding-bottom: 21px;
         line-height: 20px;
         @include f('os800');
         text-transform: uppercase;
         border-bottom: 1px solid rgba($white, .3);
         @include sm {
            font-size: 14px;
            padding-bottom: 10px; } }
      &-list {
         @include sm {
            font-size: 15px; }
         li {
            &:not(:last-child) {
               margin-bottom: 8px; }
            a {
               @include trs;
               letter-spacing: .03em;
               &:hover {
                  color: $color-1; } } } } }
   &__bottom {
      padding: 20px 0;
      font-size: 14px;
      line-height: 22px;
      background-color: #f0f0f0;
      &-container {
         display: flex;
         justify-content: space-between;
         @include xs {
            flex-direction: column; } } }
   &__logo {
      flex: 0 0 198px;
      margin-right: 40px;
      @include xs {
         margin-right: 0;
         flex: 0 1 auto;
         margin-bottom: 30px; } }
   &__copy {
      @include sm {
         display: none; } }
   &__quote {
      position: relative;
      flex: 0 0 495px;
      margin-left: auto;
      @include f('os700i');
      @include xs {
         flex: 0 1 auto;
         width: 100%; }
      &::before {
         position: absolute;
         top: -5px;
         left: 5px;
         z-index: 0;
         @include sprite('right-quote');
         content: ''; } } }
